.ageGroupsCat {
  z-index: 9;
  margin-top: 5vh;
  font-weight: lighter;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-family: "Space Grotesk", sans-serif;
  font-weight: light;
}

.ageGroupIndivCat {
  display: flex;
  font-size: x-large;
  justify-content: center;
  align-items: center;
  color: #f5f5f7;
  border-radius: 5px;
  height: 60vh;
  width: 20vw;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  font-family: "Space Grotesk", sans-serif;
}

.ageGroupIndivCat:hover {
  cursor: pointer;
}

.categoriesH1 {
  font-family: "Space Grotesk", sans-serif;
  margin-top: 0;
}

.ageTextCat {
  z-index: 9;
  text-align: center;
  align-items: center;
  margin-top: 1vh !important;
  display: flex;
  justify-content: center;
  position: relative;
  font-family: "Space Grotesk", sans-serif;
}

@media only screen and (max-width: 600px) {
  .ageGroupsCat {
    flex-direction: column;
  }

  .ageGroupIndivCat {
    width: 70vw;
    height: 40vh;
    margin-bottom: 5vh;
  }
}
@media only screen and (max-width: 900px) {
  .ageGroupsCat {
    flex-direction: column;
  }

  .ageGroupIndivCat {
    width: 70vw;
    height: 40vh;
    margin-bottom: 5vh;
  }
}
