table {
  font-family: "Space Grotesk", sans-serif;
  border-collapse: collapse;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: auto;
  margin-top: 0;
}

.ageRange {
  font-family: "Space Grotesk", sans-serif;
}

.indexH2 {
  margin-top: 0;
  font-family: "Space Grotesk", sans-serif;
}

.indexContainer {
  display: flex;
  font-family: "Space Grotesk", sans-serif;

  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;
  align-items: center;
  justify-content: center;
  z-index: 9;
  overflow: scroll;
  height: 70vh;
}
.indexText {
  margin-bottom: 0;
  margin-top: 0;
}
th {
  text-align: left;
  padding: 10px;
}
td {
  text-align: left;
  padding: 10px;
  width: 30vw;
  border-bottom: 0.5px solid black;
}
